import styled from "@emotion/styled";

export const Theme = styled.h1`
  font-size: 3rem;
  color: #62eb6a;
  font-weight: 700;
  margin-bottom: 20px;
`;

export const Text = styled.h2`
  font-size: 1.5rem;
  font-weight: 700;
  margin-bottom: 5px;
`;

export const Title = styled.input`
  width: 100%;
  height: 40px;
  font-size: 1rem;
  line-height: 40px;
  border: 1px solid #d3d3d3;
  border-radius: 12px;
  padding: 0 5px;
`;

export const Content = styled.textarea`
  width: 100%;
  height: 600px;
  font-size: 1rem;
  line-height: 40px;
  border: 1px solid #d3d3d3;
  font-family: "Noto Sans KR";
  border-radius: 12px;
  padding: 0 5px;
  resize: none;
`;

export const Submit = styled.input`
  width: 200px;
  height: 50px;
  font-size: 1.5rem;
  font-weight: 700;
  color: #fff;
  border-radius: 99px;
  background-color: #90eb63;
  border: none;
  margin-top: 10px;
  margin-bottom: 30px;
  cursor: pointer;
`;

export const errorTag = styled.p`
  font-size: 0.75rem;
  color: red;
  margin-left: 10px;
`;
